<template>
  <div class="landing-page">
    <!-- Hero and nav -->
    <div class="hero hero-wavy is-relative is-theme-primary huge-pb">
        <Navbar/>
        <canvas id="canvas"></canvas>
        <Hero/>
    </div>
    <!-- /Hero and nav -->
    <StepOrder/>
    <MyService/>
    <Pricing/>
    <Motivation/>
    <AboutUs/>
    <Portofolio/>
    <LatestNews/>
    <ContactUs/>
    <Footer/>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';

import Hero from '@/components/landing-pages/Hero';
import StepOrder from '@/components/landing-pages/StepOrder';
import MyService from '@/components/landing-pages/MyService';
import Pricing from '@/components/landing-pages/Pricing';
import Motivation from '@/components/landing-pages/Motivation';
import AboutUs from '@/components/landing-pages/AboutUs';
import Portofolio from '@/components/landing-pages/Portofolio';
import LatestNews from '@/components/landing-pages/LatestNews';
import ContactUs from '@/components/landing-pages/ContactUs';
export default {
  components: { Navbar, Hero, StepOrder, MyService, Pricing, Motivation, AboutUs, Portofolio, LatestNews, ContactUs, Footer },
  name: 'LandingPage',
  data () {
    return {
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hero.hero-wavy:before {
    background: transparent no-repeat scroll 0 bottom/100% auto;
}
</style>



